import React, {useState, useEffect} from "react";
import {storage_read, storage_write, storage_delete} from "@tomnicolosi/storage-wrap";

export default function TestStorage() {
    const [stored, setStored] = useState(null);
    const [input_val, setVal] = useState("");

    useEffect(() => {
        const stored_value = storage_read("gatsby-app");
        if(stored_value) {
            setStored(stored_value);
        }
    }, [stored, input_val]);

    function handle_change(e) {
        setVal(e.target.value);
    }

    function handle_save() {
        storage_write("gatsby-app", input_val);
        setVal("");
    }

    function handle_clear() {
        setVal("");
        setStored(null);
        storage_delete("gatsby-app");
    }

    return (
        <>
            <div className="form-group">
                <label htmlFor="storage-input" className="form-label">Enter value to store:</label>
                <input className="form-control" id="storage-input" type="text" onChange={handle_change} value={input_val} />
                <button className="btn btn-dark mt-2" disabled={!input_val} onClick={handle_save}>Save</button>
            </div>
            {
                stored 
                    ? (
                        <>
                            <hr />
                            <div className="pt-2">{stored}</div>
                            <button className="btn btn-secondary mt-2" onClick={handle_clear}>Clear</button>
                        </>
                    )
                    : null
            }
            
        </>
    );
}